@import './src/n1-main/m1-ui/common/mixins';

.board {
  @include flex(column, center, initial, wrap);
  margin: 20px;
  height: 100vh;
  border: 1px solid black;
  background: #f09c2d;

  .img {
    width: 60%;
  }

  @media screen and (max-width: 1200px) {

    .daysBox {
      flex-wrap: wrap;
    }
  }

    width: 90vw;

  .daysBox {
    @include flex(row, center, flex-start, no wrap);
    width: 100%;
    margin: 10px 0;

    .dayBox {
      min-width: 140px;
      width: 20%;
      height: auto;
      border: solid 1px black;
      margin: 5px;

      .minMax {
        @include flex(row, space-around, center, no wrap);

        .minTemp, .maxTemp {
          margin: 10px;
        }

        .celsius {
          height: 15px;
        }
      }
    }
  }
  @media screen and (max-width: 700px) {
    height: 100%;
    .listOfCities {
      column-count: 3;
      display: flow-root;
    }
  }

  .listOfCities {
    @include flex(column, space-around, flex-start, wrap);
    margin: 20px;
    height: 60%;
    @media screen and (max-width: 700px) {
      height: 100%;
        column-count: 3;
        display: flow-root;
    }
    @media screen and (max-width: 450px) {
      height: 100%;
      column-count: 2;
      display: flow-root;
    }
  }
}